import React from 'react';
import APage from './components/APage';
import BPage from './components/BPage';
import CPage from './components/CPage';
import DPage from './components/DPage';
import EPage from './components/EPage';
import Header from "./components/Header";
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import bgimg from './assets/images/bg-1.png';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

function App() {
  return (
    <Router>
      <div style={{backgroundImage: "url("+ bgimg +")",  height: '100%', width: '100%'}}>
        <Header />
        <Switch>
          <Route path="/bio" component={APage} style={{
        shadowOpacity: 0.75,
        shadowRadius: 5,
        shadowColor: 'red',
        shadowOffset: { height: 0, width: 0 },
      }} />
          <Route path="/APage" component={APage} />
          <Route path="/BPage" component={BPage} />
          <Route path="/CPage" component={CPage} />
          <Route path="/DPage" component={DPage} />
          <Route path="/EPage" component={EPage} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
