import React from "react";
import { Image, Nav, Navbar, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import '../assets/styles/header.css';
import logo from '../assets/images/216.png';
import NavbarToggle from "react-bootstrap/NavbarToggle";

export default function Header(props) {
  return (
    <div >
      <Navbar sticky="top" expand="lg" className="sidebar navItemCustomStyle"> <br />
        <Container>
          <Link to="/bio" className="nav-link" className="shadow-lg p-1 bg-white rounded" >
            <Image src={logo} rounded height="100"/>
          </Link>
          <NavbarToggle />
          <Navbar.Collapse id="responsive-navbar-nav"  >
            <Nav className="navbar-start ml-auto" >
              <Link to="/APage" className="nav-link text-light"> Home </Link>
              <Link to="/BPage" className="nav-link text-light" activeClassName="activeLinkClass"> Machine learning </Link>
              <Link to="/CPage" className="nav-link text-light" activeClassName="activeLinkClass"> Microservices </Link>
              <Link to="/DPage" className="nav-link text-light"> Something Else  </Link>
              <Link to="/EPage" className="nav-link text-light"> About Me  </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
};