import React from "react";
import { Container } from "react-bootstrap";
import Image from '../assets/images/bg-1.png';

const styles = {
    paperContainer: {
        backgroundImage: `url(${Image})`
    }
};


export default function APage(props) {
  return (
    <Container style={styles.paperContainer} >
      <h1>Home Page</h1>
    </Container>
  );
};
